const isLocalhost = Boolean(
  window.location.hostname === 'localhost' ||
  window.location.hostname === '[::1]' ||
  window.location.hostname.match(/^127(?:\.(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)){3}$/)
)

export const register = (config: any) => {
  
  if (process.env.NODE_ENV === 'production' && 'serviceWorker' in navigator) {
  
      const location: any = window.location
      const publicUrl = new URL(process.env.PUBLIC_URL, location)
      
      if (publicUrl.origin !== window.location.origin) return

      window.addEventListener('load', () => {

      const swUrl : string = `${process.env.PUBLIC_URL}/service-worker.js`

          if (isLocalhost) {

              checkValidServiceWorker(swUrl, config)
              
              navigator.serviceWorker.ready.then(() => {
                  console.log('This web app is being served cache-first by a service worker. To learn more, visit https://goo.gl/SC7cgQ')
              })

          } else {

              registerValidSW(swUrl, config)

          }

      })

  }

}

const registerValidSW = (swUrl: string, config: any) => {
  
  navigator.serviceWorker.register(swUrl).then(registration => {
      
      registration.onupdatefound = () => {
      
          const install: any = registration.installing
          
          install.onstatechange = () => {
              
              if (install.state === 'installed') {
                  
                  if (navigator.serviceWorker.controller) {
                      
                      console.log('New content is available please refresh.')

                      if (config.onUpdate)
                          config.onUpdate(registration)

                  } else {

                      console.log('Content is cached for offline use.')

                      if (config.onSuccess)
                          config.onSuccess(registration)

                  }

              }

          }

      }

  })
  .catch(error => {
      console.error('Error during service worker registration:', error)
  })

}

const checkValidServiceWorker = (swUrl: string, config: any) => {

  fetch(swUrl)
  .then(response => {
      
      if ( response.status === 404 || response.headers.get('content-type').indexOf('javascript') === -1 ) {
      
          navigator.serviceWorker.ready.then(registration => {
          
              registration.unregister().then( () => window.location.reload() )
          
          })

      } else {
      
          registerValidSW(swUrl, config)
      
      }

  })
  .catch(() => {
      console.log('No internet connection found. App is running in offline mode.')
  })
}

export const unregister = () => {
  if ('serviceWorker' in navigator)
      navigator.serviceWorker.ready.then(registration => registration.unregister())
}
